const Testimonials = () => (
  <section className="testimonials-section" id="testimonials">
    <h2>
      
      <img
        className="decoration"
        id="decoration-four"
        src="decoration.webp"
        alt=" "
        loading="lazy"
        width="192px"
        height="70px"
      />
    </h2>
    <div className="testimonial-container">
      {testimonials.map((testimonial) => (
        <Testimonial key={testimonial.name} {...testimonial} />
      ))}
    </div>
  </section>
);

export const AboutLegsSection = ({
  additionalInfos,
  showLink = true,
  showTitle = true,
  onContactSectionView
}) => {
  let infos = [
    {
      title: "",
      image: "reflection.webp",
      url: "/reflection",
      alt: "   ",
      desc:
        "   REFLECTION  .          ...",
    },
    {
      title: "",
      image: "reflexology.webp",
      url: "/reflexology",
      alt: "  ",
      desc:
        " ,      ,    ...",
    },
    {
      title: "",
      image: "balance.webp",
      url: "/balance",
      alt: " ",
      desc:
        "            ...",
    },
  ];

  if (Array.isArray(additionalInfos)) {
    infos = [...infos, ...additionalInfos];
  }

  useEffect(() => {
    if (typeof onContactSectionView === 'function') {
      onContactSectionView();
    }
  }, [onContactSectionView]);

  return (
    <section className="about-leg-section">
      {showTitle && (
        <h2>
           
          <img
            className="decoration"
            id="decoration-two"
            src="decoration.webp"
            alt=" "
            loading="lazy"
            width="192px"
            height="70px"
          />
        </h2>
      )}
      <div className="about-leg-cards">
        {infos.map((info, index) => (
          <button className="about-leg-card" key={index}>
            <Link to={info.url}>
              <img
                src={info.image}
                alt={info.alt}
                loading="lazy"
                className="about-leg-image"
              />
              <p className="about-leg-title"> {info.title}</p>
              {info.desc}
            </Link>
          </button>
        ))}
      </div>
      <br />
      <br />
      <br />
      
      {showLink && (
        <button className="general-button">
        <Link className="more" to="/about-legs">
           
        </Link>
        </button>
      )}
      <br />
      <br />
    </section>
  );
};
